.home-container {
    text-align: center;
    padding: 50px;
    background-color: #f5f5f5;
  }
  
  h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }
  
  h2 {
    font-size: 2rem;
    color: #555;
  }
  
  p {
    font-size: 1.2rem;
    margin: 20px 0;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .cta-button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.2rem;
  }
  
  .cta-button:hover {
    background-color: #0056b3;
  }
  