.projects-section {
    padding: 50px;
    background-color: #fff;
    text-align: center;
  }
  
  .project {
    margin-bottom: 40px;
  }
  
  .project h3 {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .project p {
    font-size: 1.2rem;
    color: #666;
  }
  
  .project a {
    color: #007bff;
    text-decoration: none;
    font-size: 1.1rem;
  }
  
  .project a:hover {
    text-decoration: underline;
  }
  