/* src/components/Contact.css */

.contact-section {
    padding: 50px;
    background-color: #f5f5f5;
    text-align: center;
  }
  
  .contact-section h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .contact-section p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 30px;
  }
  
  .contact-link {
    display: inline-block;
    margin: 10px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.2rem;
  }
  
  .contact-link:hover {
    background-color: #0056b3;
  }
  